import React from "react";
import './DirectoryIcon.scss'

import folder from './icons/folder.svg'

import ai from './icons/ai.svg'
//import attachment from './icons/attachment.svg'
import aviMkvMovMwv from './icons/avi-mkv-mov-mwv.svg'
import bin from './icons/bin.svg'
import csv from './icons/csv.svg'
import docx from './icons/docx.svg'
import eps from './icons/eps.svg'
import exe from './icons/exe.svg'
import html from './icons/html.svg'
import key from './icons/key.svg'
import mp3 from './icons/mp3.svg'
import mp4 from './icons/mp4.svg'
import pages from './icons/pages.svg'
import pdf from './icons/pdf.svg'
import pngJpgGif from './icons/png-jpg-gif.svg'
import ppt from './icons/ppt.svg'
import psd from './icons/psd.svg'
import rtf from './icons/rtf.svg'
import stypi from './icons/stypi.svg'
import swf from './icons/swf.svg'
import txt from './icons/txt.svg'
import webex from './icons/webex.svg'
import xlsx from './icons/xlsx.svg'
import xml from './icons/xml.svg'
import zip from './icons/zip.svg'

const map = {
    ai,
    //attachment,
    avi:aviMkvMovMwv,
    mkv:aviMkvMovMwv,
    mov:aviMkvMovMwv,
    mwv:aviMkvMovMwv,
    bin,
    csv,
    docx,
    eps,
    exe,
    html,
    key,
    mp3,
    mp4,
    pages,
    pdf,
    png:pngJpgGif,
    jpg:pngJpgGif,
    jpeg:pngJpgGif,
    gif:pngJpgGif,
    ppt,
    pptx:ppt,
    psd,
    rtf,
    stypi,
    swf,
    txt,
    webex,
    xlsx,
    xml,
    zip,
};


export default function DirectoryIcon({ name, isDirectory }) {
    const SvgComponent = isDirectory ? folder : map[getFileExtension(name.toLowerCase())] || bin;

    return <SvgComponent className="DirIcon"/>;
}

function getFileExtension(filename) {
    // Use a regular expression to match the file extension
    const match = /\.([^.]+)$/.exec(filename);
  
    // If a match is found, return the extension in lowercase; otherwise, return null
    return (match[1] || "txt").toLowerCase();
}